.container {
  width: auto;
  margin-bottom: 30px;
  padding: 20px 15px 0;
  border-top: 2px solid #f0f0f0;
}

.avatar {
  min-width: 75px;
  height: 75px;
  background-image: url(https://images.vexels.com/media/users/3/229988/isolated/lists/718055e6d4d002b6e7797af819d2a251-wild-cat-head-logo.png);
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 15px;
}

.body h3 {
  margin-top: 0;
}

.footer {
  width: 50px;
  height: 30px;
  border-radius: 5px;
  text-align: center;
  background-color: #f0f0f0;
  font-weight: 500;
  font-size: 22px;
  transition: .3s;
  margin-left: calc(100% - 55px);
}
.footer:hover {
  color: #43CB83;
  cursor: pointer;
}